@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700);
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "Nunito Sans", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100vh;
}
.User {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
  }
  .User:hover {
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }
  .User__pic {
    width: 50px;
    border-radius: 50%;
  }
  .User__details {
    display: none;
  }
  
  /* not small devices  */
  @media (min-width: 576px) {
    .User__details {
      display: block;
      padding: 0 0 0 1rem;
    }
    .User__details-name {
      margin: 0;
      color: rgba(255, 255, 255, 0.8);
      font-size: 1rem;
    }
  }
.Sidebar {
    width: 80px;
    background-color: rgba(32, 32, 35, 1);
    height: 100%;
    border-right: 1px solid rgba(189, 189, 192, 0.1);
    transition: width 0.3s;
    overflow-y: scroll;
  }
  
  /* not small devices  */
  @media (min-width: 576px) {
    .Sidebar {
      width: 320px;
    }
  }
.Main {
    flex: 1 1;
    background-color: rgba(25, 25, 27, 1);
    height: 100%;
  }
.Empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .Empty__name {
    color: #fff;
  }
  .Empty__status,
  .Empty__info {
    padding: 1rem;
  }
  .Empty__status {
    color: rgba(255, 255, 255, 0.9);
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  }
  .Empty__img {
    border-radius: 50%;
    margin: 2rem 0;
  }
  .Empty__btn {
    padding: 1rem;
    margin: 1rem 0;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 30px;
    outline: 0;
  }
  .Empty__btn:hover {
    background: rgba(255, 255, 255, 0.7);
    cursor: pointer;
  }
.App {
  height: 100%;
  display: flex;
  color: rgba(189, 189, 192, 1);
}
